<template>
  <div>
    <el-menu
      class="el-menu-demo"
      mode="horizontal"
      @select="handleSelect"
      background-color="#ffcc00"
      text-color="#6633cc"
      active-text-color="cc3366"
    >
      <el-menu-item index="1">处理中心</el-menu-item>
      <el-submenu index="2">
        <template slot="title">我的工作台</template>
        <el-menu-item index="2-1">选项1</el-menu-item>
        <el-menu-item index="2-2">选项2</el-menu-item>
        <el-menu-item index="2-3">选项3</el-menu-item>
        <el-submenu index="2-4">
          <template slot="title">选项4</template>
          <el-menu-item index="2-4-1">选项1</el-menu-item>
          <el-menu-item index="2-4-2">选项2</el-menu-item>
          <el-menu-item index="2-4-3">选项3</el-menu-item>
        </el-submenu>
      </el-submenu>
      <el-menu-item index="3" disabled>消息中心</el-menu-item>
      <el-menu-item index="4">
        <a href="JavaScript:;" target>订单管理</a>
      </el-menu-item>
      <el-menu-item class>
        <el-input placeholder="请输入内容" v-model="input" clearable></el-input>
      </el-menu-item>
      <el-menu-item class="right">
        <el-button type="text" @click="dialogVisible = true">关于本站</el-button>
        <el-dialog title="关于本站" :visible.sync="dialogVisible" width="40%" :before-close="handleClose">
          <span>这是一个基于vue2.x并利用element组件编写的页面</span>
          <span slot="footer" class="dialog-footer" style="display:flex;justify-content: center;">
            <el-button @click="dialogVisible = false" style="margin-right:7%">取 消</el-button>
            <el-button type="primary" @click="dialogVisible = false" style="margin-left:7%">确 定</el-button>
          </span>
        </el-dialog>
      </el-menu-item>
    </el-menu>
  </div>
</template>
<script>
export default {
  name: 'header',
  data () {
    return { dialogVisible: false } },
  methods: {
    handleClose (done) {
      this.$confirm('真的关闭吗？')
        .then(_ => {
          done()
        }).catch(_ => {})
    }}
}
</script>
<style scoped>
.el-menu-demo {
  padding-left: 11%;
  padding-right: 11%;
  display: flex;
  align-items: center;
  align-content: center;
}
</style>



