<template>
  <div id="content">
    <Header />
    <Carousel />
    <Weather />
    <div class="top">
      <div class="top_a">
        <div class="top_i">
          <h1>HTML 5</h1>
        </div>
        <div class="top_i">
          <h1>CSS 3</h1>
        </div>
        <div class="top_i">
          <h1>JAVASCRIPT</h1>
        </div>
        <div class="top_i">
          <h1>VUE</h1>
        </div>
      </div>
      <div class="top_a">
        <div class="top_i">
          <h1>Bootstrap</h1>
        </div>
        <div class="top_i">
          <h1>React</h1>
        </div>
        <div class="top_i">
          <h1>Angular 2</h1>
        </div>
        <div class="top_i">
          <h1>Node.js</h1>
        </div>
      </div>
    </div>
    <div class="smile">
        <span style="width:85%;background:pink">{{sm[0]}}</span>
        <el-button @click="tosmile()">切换</el-button>
    </div>
    <div class="list">
      <div class="list_a">
        <div class="list_i">
          <img src="@/assets/cont1.jpg" />
        </div>
        <div class="list_h">
            <h1>每个人的生命中都会遇到遂自己所愿，珍惜每一份缘分，珍惜每一份相遇。
              既相遇，必相惜。既有缘，必在意。用一颗善良之心去款待每一份情意，
              用一颗宽容之心去恩待每一位朋友。用一颗理解之心去诚待世人，用一颗
              仁爱之心去善待亲人。
            </h1>
        </div>
      </div>
      <div class="list_a">
        <div class="list_i">
          <img src="@/assets/cont2.jpg" />
        </div>
        <div class="list_h">
            <h1>人活着最终是为了什么?我们总在感叹，这个世界变化太快，成熟不是看你
                的年龄有多大，而是看你能担起多大的责任。敢于担当，才能赢得别人的敬重与关怀，
                赢得别人的认同与信赖。英雄因为担当而伟大，君子因为担当而崇高。
            </h1>
        </div>
      </div>
    </div>
    <Bottom />
  </div>
</template>

<script>
import Carousel from '@/components/Carousel'
import Header from '@/components/Header'
import Weather from '@/components/Weather'
import Bottom from '@/components/Bottom'
export default {
  name: 'index',
    data () {
    return {
      sm: ' '
    }
  },
  components: { Carousel, Header, Weather, Bottom },
  methods: {
    tosmile () {
      this.$ajax.get('http://hd215.api.yesapi.cn/?s=App.Common_Joke.RandOne&app_key=34B1D06BAE79A4B68E2F26B68AD59273').then(function (res) {
        var sm=res.data.data.joke[0]
      })
    }}
}
</script>

<style scoped>
#content {
  width: 100%;
  background-image: url(../assets/indexbg.jpg);
}
.top {
  width: 90%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
.top_a {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.top_i {
  width: 24%;
  line-height: 80px;
  margin-top: 1%;
  background-color: antiquewhite;
  color:cornflowerblue;
}
.smile {
    width: 60%;
    margin: 2% auto;
    text-align: left;
    display: flex;
}
.smile button {
    margin-left: 3%;
}
.list {
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
}
.list_a{
    display: flex;
    width: 100%;
}
.list_i{
    width: 70%;
    margin-top: 1%;
}
.list_i img{
    width: 100%;
}
.list_h {
    margin-left: 5%;
    display: flex;
    align-items: center;
    text-align: left;
    color: #ff8d1d;
}
</style>



