<template>
    <div class="content">
        <p>天气</p>
        <div>20℃</div>
        <div>优</div>
        <div>晴</div>
    </div>
</template>
<script>
export default {
  name: 'weather'
}
</script>

<style  scoped>
    .content{
        width: 4%;
        height: 15%;
        position: fixed;
        right: 0.5%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        top: 180px;
        z-index: 3;
        font-weight: bold;
        font-size: 1.2rem;
        color: brown;
        border: 1px solid pink;
        text-align:center;
}
</style>



