<template>
  <div>
    <el-carousel height="500px" :interval="3000" arrow="hover" id="el-carousel" indicator-position="none">
      <el-carousel-item id="el-carousel__item">
        <img src="../assets/banner1.jpg" alt srcset />
      </el-carousel-item>
      <el-carousel-item id="el-carousel__item">
        <img src="../assets/banner2.jpg" alt srcset />
      </el-carousel-item>
      <el-carousel-item id="el-carousel__item">
        <img src="../assets/banner3.jpg" alt srcset />
      </el-carousel-item>
      <el-carousel-item id="el-carousel__item">
        <img src="../assets/banner4.jpg" alt srcset />
      </el-carousel-item>
      <el-carousel-item id="el-carousel__item">
        <img src="../assets/banner5.jpg" alt srcset />
      </el-carousel-item>
      <el-carousel-item id="el-carousel__item">
        <img src="../assets/banner6.jpg" alt srcset />
      </el-carousel-item>
      <el-carousel-item id="el-carousel__item">
        <img src="../assets/banner7.jpg" alt srcset />
      </el-carousel-item>
    </el-carousel>
  </div>
</template>
<script>
export default {
  name: 'carousel',
  data () {
    return {
      imgs: [{ url: '../assets/banner1.jpg' }]
    }
  }
}
</script>

<style scope>

#el-carousel__item {
  width: 100%;
  height: 100%;
}

#el-carousel__item img {
  width: 100%;
  height: 100%;
}
</style>



